header{
    background-color: $bg-green;
    padding: 2rem 0 2rem 0;
    height: 100vh;
    min-width: 296px;
}

.logo-box{
    margin-bottom: $header-margin-bottom;
}

.user{
    padding-left: 2rem;
    img{
        width: 40px;
        height: 40px;
        border-radius: 100%;
        margin-right: $header-margin-right;
    }
    div{
        .user-name{
            font-weight: bold;
        }
        .user-email{
            color: $gray01;
        }
    }
}

.icon-li{
    margin-bottom: $header-margin-bottom;
    font-weight: bold;
    a{
        color: $dark;
        transition: .2s;
        &:hover{
            color: $hover-green;
        }
        &:focus{
            color: $hover-green;
        }
        p{
            line-height: $header-line-height;
        }
    }
    span{
        margin-right: $header-margin-right;
    }
}

.sidebar .menu a.active{
    color: $hover-green;
    box-shadow: inset 4px 0 0 $hover-green;
}

.sidebar,.menu{
    a{
        padding-left: 2rem;
    }
}