.header-box{
    padding:$add-admin-padding;
}

.pop-up-h5{
    font-size: $add-admin-h1;
    color: $dark;
    line-height: 33px;
    font-weight: 450;
}

.close-box{
    opacity: 100;
}

.form-area{
    padding: 0 2rem 2rem 2rem;
    h6{
        padding-bottom: 1rem;
        font-size: $add-font-size01;
        color: $dark;
    }
}

.label-box{
    font-size: 14px;
    font-weight: 400;
    color: $dark;
}

.input-box{
    border: 1px solid $dark;
    height: 3.125rem;
    border-radius: .125rem;
}

.add-line{
    margin: 24px 0 24px 0;
    border: .5px solid $border-green;
}

.modal-border{
    border: 1px solid $border-green;
    border-radius: .125rem;
}

.s-headline{
    .success{
        color: $hover-green;
    }
    .warning{
        color: $alert;
    }
    p{
        font-size: 1rem;
    }
}

.date-p{
    font-size: .875rem;
    font-weight: 400;
}

.btn-verification{
    width: 100%;
    border-radius: .125rem;
    height: 3.125rem;
    border: 1px solid $border-green;
    &:hover{
        background-color:$hover-green;
        border: 1px solid $hover-green;
    }
    &:focus{
        box-shadow: none;
    }
}

.btn-option{
    color: $dark;
    width: 100%;
    height: 3.125rem;
    border: 1px solid $dark;
    background-color: $white;
    &:hover{
        background-color: $bg-green;
        color: $dark;
        border: 1px solid $dark;
    }
    &:focus{
        outline: none;
        box-shadow: none;
    }
}

.form-check-label{
    color: $dark;
    font-weight: 400;
}

.form-check-input{
    margin-top: .2rem;
}

.btn-deactivate{
    color: $alert;
    border: 1px solid $alert;
    &:hover{
        background-color: $alert;
        border: 1px solid $alert;
    }
}

.update-btn,.cancel-btn{
    padding: .5rem 2rem .5rem 2rem;
}

.update-btn{
    background-color: $hover-green;
    border: 1px solid $hover-green;
}

.cancel-btn{
    background-color: $white;
    border: 1px solid $hover-green;
    color: $hover-green;
}
